import { Store } from "../core/sihyonn";

// Ïù¥Í≤å Í∞±Ïã†ÎêòÎ©¥ sihyonnÏóê Store Í∞úÎÖêÏùÑ ÌÜµÌï¥ÏÑú setÌï®Ïàò ÎèôÏûëÌï¥ÏÑú Í∞±Ïã†ÎèÑ ÎêòÍ≥†...
const store = new Store({
  searchText: "",
  page: 1,
  pageMax: 1,
  movies: [],
  movie: {},
  loading: false,
  message: "Search for the movie title!",
});
export default store;

export const searchMovies = async (page) => {
  store.state.loading = true;
  // Îì§Ïñ¥Ïò® ÌéòÏù¥ÏßÄÎ≤àÌò∏Î°ú ÌéòÏù¥ÏßÄ Í∞±Ïã†
  store.state.page = page;
  if (page === 1) {
    // pageÍ∞Ä 1Ïù¥ÎùºÎäîÍ±¥ Îòê ÏÉàÎ°úÏö¥ Í≤ÄÏÉâÏùÑ Ìï¥ÏÑú Îì§Ïñ¥Ïò®Í±∞ÎãàÍπå ÌéòÏù¥ÏßÄ ÏÉÅÌÉúÎäî 1Î°ú, Î≥¥Ïó¨Ï°åÎçò Î¨¥ÎπÑÎì§ÏùÄ ÎπÑÏõåÏ§å
    store.state.movies = [];
    store.state.message = "";
  }
  try {
    const res = await fetch(
      `https://omdbapi.com/?apikey=7035c60c&s=${store.state.searchText}&page=${page}`
    );
    const { Search, totalResults, Response, Error } = await res.json();

    if (Response === "True") {
      // Î∞îÎ°ú Search ÌïòÎ©¥ Ï∂îÍ∞ÄÏ†ÅÏù∏ ÌéòÏù¥ÏßÄÏóê Îã¥Í∏¥ Ïï†Îì§ Î™ªÍ∞ÄÏ†∏Ïò§ÎãàÍπå Ï†ÑÍ∞úÏó∞ÏÇ∞Ïûê
      store.state.movies = [...store.state.movies, ...Search];

      // Ï†ÑÏ≤¥Îç∞Ïù¥ÌÑ∞ Í∞úÏàòÏóêÏÑú 10Í∞úÏî© Í∞ÄÏ†∏Ïò§ÎãàÍπå 10ÏúºÎ°ú ÎÇòÎàÑÎ©¥ ÏÜåÏàòÏ†êÏù¥Îùº Ïò¨Î†§Ï§òÏïºÌï® => ÏÑúÎ≤ÑÍ∞Ä Í∞ÄÏßÄÍ≥† Ïò¨ Ïàò ÏûàÎäî ÏµúÎåÄ ÌéòÏù¥ÏßÄÎ≤àÌò∏
      store.state.pageMax = Math.ceil(Number(totalResults) / 10);
    } else {
      store.state.message = Error;
    }
  } catch (error) {
    console.log("searchMovies error:", error);
  } finally {
    store.state.loading = false;
  }
};

export const getMovieDetial = async (id) => {
  try {
    const res = await fetch(
      `https://omdbapi.com?apikey=7035c60c&i=${id}&plot=full`
    );
    store.state.movie = await res.json();
  } catch (error) {
    console.log("getMovieDetails error!", error);
  }
};
