import { Component } from "../core/sihyonn";
import movieStore, { searchMovies } from "../store/movie";

export default class Search extends Component {
  render() {
    this.el.classList.add("search");
    this.el.innerHTML = /* html */ `
      <input
      value="${movieStore.state.searchText}"
      placeholder="Enter the movie title to search!üòä"/>
      <button class="btn btn-primary">Search!</button>
    `;
    // Í≤ÄÏÉâÏñ¥Ï∞ΩÏóê ÏûÖÎ†•ÌïòÎ©¥ ÏûÖÎ†•Îêú valueÍ∞íÏúºÎ°ú ÏÉÅÌÉú Í∞±Ïã†
    const inputEl = this.el.querySelector("input");
    inputEl.addEventListener("input", () => {
      // movie.jsÏóê ÏûàÎäî storeÏóê searchTextÏóê ÏûÖÎ†•Îêú Î∞∏Î•òÍ∞íÎÑ£Ïñ¥Ï£ºÍ∏∞(Í∞±Ïã†)
      movieStore.state.searchText = inputEl.value;
    });

    // ÏûÖÎ†•ÌïòÍ≥† ÌÇ§Î≥¥ÎìúÎ•º Ïπ† Í≤ΩÏö∞ ÏòÅÌôîÎç∞Ïù¥ÌÑ∞Í∞ÄÏ†∏Ïò§Îäî fetchÌï®ÏàòÏûëÎèôÏãúÌÇ§Í≤å
    inputEl.addEventListener("keydown", (event) => {
      // ÏûÖÎ†•Ìïú ÎÇ¥Ïö©Ïù¥ ÏûàÍ≥† ÏóîÌÑ∞ÌÇ§Î•º Ï≥§ÏùÑÎïåÎäî Ìï®ÏàòÌò∏Ï∂úÌï®
      if (event.key === "Enter" && movieStore.state.searchText.trim()) {
        searchMovies(1);
      }
    });

    // ÏûÖÎ†§ÌïòÍ≥† Í≤ÄÏÉâ Î≤ÑÌäºÏùÑ ÎàÑÎ•¥Îäî Í≤ΩÏö∞
    const btnEl = this.el.querySelector(".btn");
    btnEl.addEventListener("click", () => {
      // Í≤ÄÏÉâÏñ¥Í∞Ä ÏûàÎäîÏßÄÎßå ÌôïÏù∏
      if (movieStore.state.searchText.trim()) {
        searchMovies(1);
      }
    });
  }
}
