import { Component } from "../core/sihyonn";

export default class TheHeader extends Component {
  constructor() {
    super({
      tagName: "header",
      state: {
        menus: [
          {
            name: "Search",
            href: "#/",
          },
          {
            name: "Movie",
            href: "#/movie?id=tt4520988",
          },
          {
            name: "About",
            href: "#/about",
          },
        ],
      },
    });
    window.addEventListener("popstate", () => {
      this.render();
    });
  }
  render() {
    this.el.innerHTML = /*html*/ `
    <a href="#/" class="logo">
      <span>OMDbAPI</span>.COM
    </a>
    <nav>
      <ul>
      <!-- Ïó¨Í∏∞ÏÑú Ïì∏Í±∞ superÏùò stateÏóê menusÎùºÍ≥†ÌïòÍ≥† ÎßåÎì§Ïñ¥Ï£ºÍ∏∞ -->
      ${this.state.menus
        .map((menu) => {
          // ÏøºÎ¶¨Ïä§Ìä∏ÎßÅÏù¥ ÏûàÏúºÎ©¥ ÏùºÏπòÏó¨Î∂Ä ÏïåÍ∏∞Ïñ¥Î†§Ïö∞ÎãàÍπå ÏøºÎ¶¨Ïä§Ìä∏ÎßÅ Ï†úÍ±∞Îêú Ïù¥ÎèôÌï† Ï£ºÏÜåÏôÄ, ÌòÑÏû¨ÌéòÏù¥ÏßÄÏ£ºÏÜå
          const href = menu.href.split("?")[0];
          const hash = location.hash.split("?")[0];
          const isActive = href === hash;

          return /*html*/ `
        <li>
          <a class="${isActive ? "active" : ""}" href="${menu.href}">
          ${menu.name}
        </a>
        </li>
        `;
        })
        .join("")}
      </ul>
    </nav>
    <a href="#/about" class="user">
      <img src="https://heropy.blog/css/images/logo.png" alt="User" />
    </a>


    `;
  }
}
