import { Component } from "../core/sihyonn";
import movieStore, { searchMovies } from "../store/movie";

export default class MovieListMore extends Component {
  constructor() {
    super({
      tagName: "button",
    });

    movieStore.subscribe("pageMax", () => {
      // movieStore.state.page
      // movieStore.state.pageMax
      const { page, pageMax } = movieStore.state;
      // Îß•Ïä§Í∞Ä Îçî ÌÅ∞Í±¥ ÏïÑÏßÅ Í∞ÄÏ†∏Ïò¨ÏàòÏûàÎäî ÌéòÏù¥ÏßÄÍ∞Ä Îçî ÎÇ®ÏïòÎã§ ÏùòÎØ∏ÎãàÍπå
      // hideÎ•º Ï†úÍ±∞ÌïúÎã§ => ÌôîÎ©¥Ïóê Î≥¥Ïó¨ÏßÑÎã§ ÏùòÎØ∏. Ïà®ÍπÄÏùÑ ÏóÜÏï§Í±∞ÎãàÍπå
      if (pageMax > page) {
        this.el.classList.remove("hide");
      } else {
        this.el.classList.add("hide");
      }
    });
  }
  render() {
    this.el.classList.add("btn", "view-more", "hide");
    this.el.textContent = "View more...";

    this.el.addEventListener("click", async () => {
      this.el.classList.add("hide");
      await searchMovies(movieStore.state.page + 1);
    });
  }
}
